.home {
  display: grid;
  max-width: 1200px;
  grid-template-columns: 60px 1fr 374px;
  overflow: hidden;
  margin: 58px auto 50px;
  position: relative;

  // &>.profil-page {
  //   display: initial;
  //   max-width: 100%;
  // }

  @media screen and (max-width: 990px) {
    grid-template-columns: 60px 1fr;
    margin: 58px 20px 50px
  }

  @media screen and (max-width: 590px) {
    grid-template-columns: 1fr;
    margin: 58px 8px 50px;
  }

  .main {
    .home-header {
      margin: 0 auto 12px;
      text-align: center;

      .connection-form {
        background: $color-4;
        border-radius: 20px;
        padding: 20px;
        // margin-top: 6.6rem;

        @media screen and (max-width: 590px) {
          grid-template-columns: 1fr;
          margin: 58px 8px 50px;
        }

        ul {
          display: flex;
          justify-content: center;
          margin-bottom: 18px;

          li {
            cursor: pointer;
            border-radius: 20px;
            padding: 12px 14px;
            transition: .2s;
            margin: 0 8px;
          }

          .active-btn {
            background: $color-1;

            &:hover {
              background: $color-2;
            }
          }

          li:not(.active-btn) {
            &:hover {
              transform: translateX(3px)
            }
          }
        }

        form {
          input {
            padding: 10px 14px;
            margin: 0;
            font-size: 1.2rem;
            font-family: $font-2;
            border-radius: 20px;

            &:focus {
              box-shadow: 0 0 3px rgba(51, 51, 51, 0.2);
            }
          }

          input[type=submit] {
            padding: 14px 14px;
            border-radius: 20px;
          }
        }
      }
    }
  }

  .right-side {
    position: relative;
    width: 100%;
    margin: 0px 0 0 14px;

    @media screen and (max-width: 990px) {
      display: none;
    }

    .right-side-container {
      position: fixed;

    }

  }
}

