.fa-globe {
  color: #FD2D01;
}

.fa-user {
  color: #FD2D01;
}

.fa-crosshairs {
  color: #FD2D01;
}

.fa-landmark {
  color: #FD2D01;
}


.left-nav-container {
  position: relative;
  margin-top: 10px;
  // ;

  .icons {
    position: fixed;
    z-index: 11;


    @media screen and (max-width: 590px) {
      margin: 2px 0 0 -9px;
      width: 100%;
    }

    .icons-bis {
      position: absolute;
      left: 10px;


      @media screen and (max-width: 590px) {
        display: flex;
        justify-content: space-evenly;
        padding: 0px 20px 20px 20px;
        background-color: $white;
        position: relative;
        top: -6px;
        left: -5px;
      }

      a {
        transition: 0.1s;
        display: inline-block;

        &:before {
          transition: .15s ease;
          transform: scale(0);
          transform-origin: center;
          z-index: -10;
          content: "";
          height: 45px;
          width: 45px;
          position: absolute;
          // background: $color-4;
          border-radius: 50px;
        }

        &:hover {
          transform: scale(1.02);
        }

        &:hover::before {
          transform: scale(1);

          @media screen and (max-width: 590px) {
            background: $color-1;
            height: 34px;
            width: 34px;
          }
        }

        i {
          scale: 1.9;
        }

        // .fa-unlock {
        //   @media screen and (max-width: 590px) {
        //     padding-left: 15px;
        //   }
        // }

        .fa-crosshairs {
          @media screen and (max-width: 590px) {
            padding-left: 15px;
          }
        }

        img {
          @media screen and (max-width: 590px) {
            height: 34px;
            width: 34px;
          }
        }
      }

      .active-left-nav {
        position: relative;

        &::after {
          content: "";
          height: 40px;
          width: 5px;
          position: absolute;
          background: $color-1;
          left: -7px;
          bottom: 8px;
          border-radius: 40px;
          transform: scaleY(0);
          animation: scale 1.5s ease forwards;

          @keyframes scale {
            to {
              transform: scaleY(1);
            }
          }

          @media screen and (max-width: 590px) {
            height: 33px;
            left: -8px;
          }
        }
      }
    }
  }
}